        description: '<p>Sahaja Yoga Center RE-OPENED!</p> <p>Free Meditation class for BEGINNERS &amp; ADVANCED !</p> <p>A Global initiative to change the world by changing ourselves.</p> <p>Info: mobile/whatsapp: [masked] (Ganesh)</p> <p>Video: <a href=\'https://www.youtube.com/watch?v=DrCcHm_M16Y&amp;list=PL11600E36E6E9E4AC\' class=\'embedded\'>https://www.youtube.com/watch?v=DrCcHm_M16Y&amp;list=PL11600E36E6E9E4AC</a></p> <p>CoolCheck-Website (<a href=\'http://coolcheck.org/\' class=\'linkified\'>http://coolcheck.org/</a>)</p> <p>Life only gets better. You’re about to start an amazing journey.</p> <p>However, it is special because it has the power to completely and totally relax your mind and body while energizing it, leaving you in a state where you are able to do anything and everything.<br/>Now we get down to the technicalities. The first step to achieving a state of meditation is simply desire; you have to want it. For your first meditation, it is best to find a quiet place where you can sit comfortably. Take your shoes, glasses, or any extra items off; basically get ready to relax.<br/>Now you know the body is made of three channels and 7 energy centers. An imbalance in any of these is due to the life we lead. So the first thing we shall do is to cleanse these Centers.</p> ',
