        description: '<p>Herzlich Willkommen zu Austausch statt Tatort!<br/>Keine Lust, den Abend allein zu verbringen? Hier lernst du inspirierende Menschen und ihre Geschichten kennen!</p> <p>Diese Gesprächsrunde findet regelmäßig für alle Menschen der Community statt. Das Thema bestimmst du!</p> <p>Wie waren die letzten Monate? Wie geht es dir? Was beschäftigt dich? Was liegt dir auf dem Herzen? Worüber möchtest du dich austauschen?<br/>Und was ist avec+ überhaupt?</p> <p>In verschiedenen Runden werden wir uns gegenseitig kennenlernen, austauschen und inspirieren.</p> <p>+community<br/>Online via Zoom | 20 - 22.00 Uhr | um eine Spende wird gebeten</p> <p>Die Teilnehmerzahl ist begrenzt auf 20 Personen. Bitte melde dich an, um dabei sein zu können. <a href=\'https://www.eventbrite.de/e/community-conversation-mitmenschen-mitgefuhl-miteinander-tickets-115619504049\' class=\'linkified\'>https://www.eventbrite.de/e/community-conversation-mitmenschen-mitgefuhl-miteinander-tickets-115619504049</a></p> <p>Veranstalterin ist Leona Petereit. Sie ist psychologische Beraterin, ausgebildete und zertifizierte Coachin + Trainerin sowie die Gründerin von avec+.</p> <p>Was ist [avec+]?<br/>Avec heißt auf Französisch mit, plus heißt mehr. Bei [avec+] kreuzen sich Wege, stellen sich Fragen, finden sich Antworten, ergeben sich Chancen und verbinden sich Menschen. Arm mit reich. Jung mit alt. Frau mit Mann mit Queer. Herz mit Verstand. Ja mit Nein. Wir setzen uns ein für Wirtschaft mit Sinn und für Unternehmen mit Menschlichkeit. Wir fördern Personen mit Persönlichkeit und Bewegung mit Fortschritt. Wir glauben daran, dass das Zeigen von Gefühlen eine Stärke ist. Wir stehen für mehr offline statt online und für Zukunft mit Zukunft. Kurzum: für analoges Miteinander statt digitales Nebeneinander.</p> <p>[avec+] schafft Räume des Austauschs, in denen Menschen sich entfalten und gemeinsam entwickeln können. In Einzelcoachings, Gruppengesprächen, Workshops sowie auf Events und Retreats widmen sich die Teilnehmenden unter Leitung von Leona Petereit Themen wie Selbsterkenntnis, Sinnfindung, Liebe, Arbeit oder sozialem Engagement. Dabei bereichern psychologische, philosophische, interkulturelle und künstlerische Perspektiven die Veranstaltungen, um in sensibler Atmosphäre neue Denkanstöße zu geben. Empathie und Emotion statt Stigma und Tabu. Reden wir in echt, fühlen wir von Mensch zu Mensch, leben wir miteinander!</p> <p>Bei weiteren Fragen oder Anmerkungen melde dich gern oder schreib eine Email an [masked].</p> <p>Weitere Infos unter www.avecplus.de<br/>Facebook + Instagram @avecpluscommunity</p> ',
