        description: '<p>Dear readers,</p> <p>Next time I\'d love to discuss two short novels by Dostoyevsky with you - Notes from Underground and The Double. They make together only 287 pages :-).</p> <p>Here\'s a short description from goodreads:</p> <p>‘It is best to do nothing! The best thing is conscious inertia! So long live the underground!’Alienated from society and paralysed by a sense of his own insignificance, the anonymous narrator of Dostoyevsky’s groundbreaking Notes from Underground tells the story of his tortured life. With bitter sarcasm, he describes his refusal to become a worker in the ‘ant-hill’ of society and his gradual withdrawal to an existence ‘underground’. The seemingly ordinary world of St Petersburg takes on a nightmarish quality in The Double when a government clerk encounters a man who exactly resembles him – his double perhaps, or possibly the darker side of his own personality. Like Notes from Underground, this is a masterly study of human consciousness. Jessie Coulson’s introduction discusses the stories’ critical reception and the themes they share with Dostoyevksy’s great novels.</p> <p><a href=\'https://www.goodreads.com/book/show/17881.Notes_from_Underground_The_Double?ac=1&amp;from_search=true&amp;qid=PIIUNP3Snm&amp;rank=2\' class=\'linkified\'>https://www.goodreads.com/book/show/17881.Notes_from_Underground_The_Double?ac=1&amp;from_search=true&amp;qid=PIIUNP3Snm&amp;rank=2</a></p> ',
