        link: 'https://www.meetup.com/Co-Dining/events/272591460/',
        description: '<p>Ausgehen, neue Leute Kennenlernen und feiern. Wir haben es alle lange gemisst.</p> <p>Am Freitagabend ist es endlich soweit wir wollen uns wieder treffen. Gutes Essen genießen und ein geselliges Miteinander erleben. Wer dabei sein möchte bitte anmelden.</p> <p>WAS DICH ERWARTET:<br/>Ich bin Gastgeberin aus Leidenschaft und glaube, dass wir zusammen mehr erreichen können. Zu diesem Zweck habe ich das togather Anfang des Jahres eröffnet.</p> <p>Wünscht du dir in einer Wohlfühlatmosphäre genussvoll und in Muße speisen zu können?</p> <p>Willst du dich in einem geselligen Rahmen mit Gleichgesinnten austauschen und dein soziales wie berufliches Netzwerk erweitern?</p> <p>Im togather lernst du neue interessante Menschen kennen, führst vielseitige Gespräche und gehst anschließend beschwingt und motiviert in deinen Tag zurück. Du gewinnst Lebensqualität durch liebevoll zubereitetes Essen in gemütlicher Atmosphäre.</p> <p>Klingt das gut? Dann bis Freitag!</p> ',
