        link: 'https://www.meetup.com/Philosophisches-BEZIEHUNGsCafe/events/272609728/',
        description: '<p>In der Corona Krise haben wir in der Gesellschaft viele Themen neu erleben und betrachten können. Einiges hat sich verändert - in der Gesellschaft und auch in unseren Beziehungen.</p> <p>Daraus hat sich für uns eine spannende Frage ergeben, die wir für diesen Abend als Thema haben werden:</p> <p>Ist die Beziehung das Abbild der Gesellschaft? Ist die Gesellschaft das Abbild unserer Beziehungen? Wo sieht sich jeder einzelne in seinen Beziehungen und damit in der Gesellschaft?</p> <p>Herzliche Einladung in einen lebendigen Dialog zu gehen.</p> <p>Falls eine größere Gruppe zusammen kommt, können wir die Gruppen auch teilen und in eigene Räume gehen (Zoom). Und uns später wieder treffen um unsere Kernpunkte zu teilen.</p> <p>Wir freuen uns auf Euch!</p> <p>Jeanette und Daniel</p> <p>Zoom Link: <a href=\'https://zoom.us/meeting/register/tJYpf-2pqzouHdCgDzLvYkS6xmUC0xK46oPA\' class=\'linkified\'>https://zoom.us/meeting/register/tJYpf-2pqzouHdCgDzLvYkS6xmUC0xK46oPA</a></p> ',
